import Vue from 'vue';
import Router from 'vue-router';
import releases from './components/releaseComponent.vue';
import Ping from './components/Ping.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'releases',
      component: releases,
    },
    {
      path: '/ping',
      name: 'Ping',
      component: Ping,
    },
  ],
});
