<script>
import axios from 'axios';
import Alert from './Alert.vue';
import NotesList from './nn.vue';

export default {
    components: {
        "notes-list": NotesList
    },

    data() {
        return {
            releases: [],
            message: '',
            showMessage: false,
            show: false,
            releaseToAdd: {
                id: '',
                releaseNotes: [],
                author: '',
                releaseDate: ''
            }
        }
    },
    
    methods: {
        getReleases() {
        const path = 'http://localhost:5000/releases';
        axios.get(path)
            .then((res) => {
            this.releases = res.data.releases;
            console.log('getReleases : this.releases = ', this.releases);
            })
            .catch((error) => {
            console.error(error);
            });
        },
        addRelease(payload) {
            const path = 'http://localhost:5000/releases1';
            axios.post(path, payload)
                .then(() => {
                    // this.getReleases();
                    this.message = 'Release added!';
                    this.showMessage = true;
                })
                .catch((error) => {
                console.log(error);
                // this.getReleases();
            });
        },

        initForm() {
            this.releaseToAdd.id = '';
            this.releaseToAdd.releaseNotes = [];
            this.releaseToAdd.releaseDate = '';
            this.releaseToAdd.author = '';

            // this.editForm.id = '';
            // this.editForm.releaseNotes = [];
            // this.editForm.releaseDate = '';
            // this.editForm.author = '';
        },

        onSubmit(evt,releaseToAdd) {
            // evt.preventDefault();
            this.$refs.addReleaseModal.hide();
            
            // releaseNotesToAdd = [];
            console.log("this.evt = ", evt);
            
            const payload = {
                id: evt.id,
                releaseNotes: evt.releaseNotes,
                releaseDate: evt.releaseDate,
                author: evt.author,
            };
            this.releaseToAdd = payload;
            this.addRelease(payload);
            this.initForm();
        },

        onReset(evt) {
            evt.preventDefault();
            this.$refs.addReleaseModal.hide();
            this.initForm();
        },
        show_notes(){
          this.show = !this.show;
        }
    },
    created() {
        this.getReleases();
    },
}
</script>

