<template>
  <div>
    <todo-list-items v-bind:releaseNotes="tasks"></todo-list-items>
    <todo-btn-actions placeholder="Add new note" v-on:new-item-added="addToList"></todo-btn-actions>

    <br />* Click to remove
  </div>
</template>

<script>
import NotesListItems from "./NotesList";
import AddButtonActions from "./ActionList";

export default {
  components: {
    "todo-list-items": NotesListItems,
    "todo-btn-actions": AddButtonActions
  },
  props: {
        initialTasks: Array
  },
  data() {
    return {
      tasks: this.initialTasks
    };
  },

  methods: {
    addToList(new_item) {
      console.log("new_item = ", new_item);
      this.tasks.push(new_item);
    }
  }
};
</script>

